import {Component, OnInit} from '@angular/core';
import {Dude, DudeService} from "../dude.service";
import {Debt, DebtService} from "../debt.service";
import {Payment, PaymentService} from "../payment.service";
import {FormControl} from '@angular/forms';

@Component({
  selector: 'app-dept-overview',
  templateUrl: './dept-overview.component.html',
  styleUrls: ['./dept-overview.component.css']
})
export class DeptOverviewComponent implements OnInit {
  dudes: Dude[] = [];
  selectedDude: Dude;
  dudeToPay: Dude;
  paymentAmount: number = 0;
  debtByRelation: Map<Dude, Debt[]> = new Map<Dude, Debt[]>();

  dudeAutoComplete: FormControl = new FormControl();

  loaded: Boolean = false;

  constructor(private dudeService: DudeService, private debtService: DebtService, private paymentService: PaymentService) {
  }

  ngOnInit() {
    this.getDudes();
  }

  getDudes(): void {
    this.dudeService.getDudes()
      .subscribe(dudes => {
        this.dudes = dudes;
        for (let debtor of this.dudes) {
          this.updateDebt(debtor);
        }
      });
  }

  private updateDebt(debtor: Dude) {
    let debts: Debt[] = [];
    this.debtByRelation.set(debtor, debts);

    this.dudes.filter(dude => dude !== debtor)
      .forEach(owner => {
        this.debtService.getDebt(owner.id, debtor.id)
          .subscribe(debt => {
              debts.push(debt);
            }
          );
      })
  }

  getDeptForDude(dude: Dude): Debt[] {
    let debts = this.debtByRelation.get(dude);
    return debts;
  }

  getADude(): Dude {
    return this.dudes[0];
  }

  getDudesExceptSelected(): Dude[] {
    return this.dudes.filter(dude => dude !== this.selectedDude);
  }

  submitPayment(): void {
    let payment = new Payment(new Date(), "asd", this.paymentAmount, this.selectedDude);

    this.paymentService.savePayment(payment).subscribe();
  }
}



